<template>
  <div>
  <section>
    <b-field label="Find a Contact">
      <b-autocomplete
        :data="data"
        placeholder="e.g. Fight Club"
        field="title"
        :loading="isFetching"
        :check-infinite-scroll="true"
        :value="name"
        @typing="getAsyncData"
        @select="option => selected = option"
        @infinite-scroll="getMoreAsyncData">

        <template slot-scope="props">
          <div class="media">
            <div class="media-content">
              {{ props.option.first_name }} {{ props.option.last_name }}
            </div>
          </div>
        </template>
      </b-autocomplete>
    </b-field>
  </section>
    <section v-if="selected!=null">
    <div class="column is-center">
    <app-contact  :contact="selected"></app-contact>
    </div>
    </section>
  </div>
</template>

<script>
    import AppContact from './Contact'
    import axios from 'axios'
    import debounce from 'lodash/debounce'

    export default {
        components:{
          AppContact
        },
        props: {
            activeTab: {
                type: Number,
                required:true
            },
        },
        data() {
            return {
                data: [],
                selected: null,
                isFetching: false,
                name: '',
                page: 1,
                totalPages: 1
            }
        },
        methods: {
            // You have to install and import debounce to use it,
            // it's not mandatory though.
            getAsyncData: debounce(function (name) {
                // String update
                if (this.name !== name) {
                    this.name = name
                    this.data = []
                    this.page = 1
                    this.totalPages = 1
                }
                // String cleared
                if (!name.length) {
                    this.data = []
                    this.page = 1
                    this.totalPages = 1
                    return
                }
                // Reached last page
                if (this.page > this.totalPages) {
                    return
                }
                this.isFetching = true
                const words=name.split(' ');


                axios.post('/contactSearch?page='+this.page,{
                    first_name:words[0],
                    last_name:words[1]
                })
                    .then(response => {
                        var x=response.data.data

                        x.forEach(item => this.data.push(item))
                        this.page++
                        this.totalPages = response.data.total
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    .finally(() => {
                        this.isFetching = false
                    })

                    //  this.$http.get(`https://api.themoviedb.org/3/search/movie?api_key=bb6f51bef07465653c3e553d6ab161a8&query=${name}&page=${this.page}`)
                    // .then(({ data }) => {
                    //     data.results.forEach((item) => this.data.push(item))
                    //
                    //     this.page++
                    //     this.totalPages = data.total_pages
                    // })
                    // .catch((error) => {
                    //     throw error
                    // })
                    // .finally(() => {
                    //     this.isFetching = false
                    // })
            }, 500),
            getMoreAsyncData: debounce(function () {
                this.getAsyncData(this.name)
            }, 250)
        },
        // activated: function(){
        //     console.log('activated', this._inactive, this.item)
        // },
        watch: {
            activeTab(newValue, oldValue) {
                // if(newValue!=2){
                    this.data= []
                    this.selected= null
                    this.isFetching= false
                    this.name= ''
                    this.page= 1
                    this.totalPages=1
                    // console.log('dsa');
                // }
                // console.log(newValue);
            }
        },
    }
</script>




