<script src="../store/store.js"></script>
<template>
  <div class="showButton">

    <b-collapse :open="false" :aria-id="'contentIdForc'+contact.id">
      <!--      <div class="container">-->
      <b-button size="is-medium"
                @click.once="getMyNumbers(contact.id)"
                class="button light numberq showButton"
                slot="trigger" :aria-controls="'contentIdForc'+contact.id"
      >
        {{contact.first_name+' '+contact.last_name}}
      </b-button>
      <!--      </div>-->
      <div class="buttons">
          <b-button size="is-small" type="is-primary" outlined>Edit Contact</b-button>
          <b-button size="is-small" type="is-danger" outlined icon-right="delete">Delete Contact</b-button>

        </div>
      <div>
        <div class="notification">
          <b-collapse :open="false" :aria-id="'contentIdFormNumber'">
            <b-button size="is-small"
                      class="is-primary button"
                      type="is-info"
                      outlined
                      slot="trigger" :aria-controls="'contentIdFormNumber'">
              Add New Number
            </b-button>
            <div class="column">
              <form @submit.prevent="addNumber">
                <b-field label="number" message="enter the number you input">
                  <b-input v-model="number"></b-input>
                </b-field>
                <b-button tag="input"
                          native-type="submit"
                          value="add"/>
              </form>
            </div>
          </b-collapse>
          <p type="is-warning">:: {{contact.address}} </p>
          <div v-for="(number,index) in numbers" :key="number.id">
            <div>
              <b-tag size="is-medium" type="is-warning">{{number.number}}</b-tag>
            </div>
            <b-button @click="editNumber(number)" size="is-small" type="is-info">Edit number</b-button>
            <b-button @click="deleteNumber(number)" size="is-small" type="is-danger">Delete number</b-button>
          </div>
        </div>
      </div>
    </b-collapse>
  </div>
</template>
<script>
    export default {
        data() {
            return {
                number: ''
            }
        },
        props: {
            contact: {
                type: Object,
                required: true
            },
        },
        methods: {
            getMyNumbers(contact_id) {
                this.$store.dispatch('getMyNumbers', contact_id);
            },
            editNumber(number) {
                this.$store.dispatch('editNumber', {
                    'id': number.id,
                    'contact_id': number.contact_id,
                    'number': '999',
                });
            },
            deleteNumber(number) {
                this.$store.dispatch('editNumber', number);
            },
            addNumber() {
                console.log(this.contact.id);
                this.$store.dispatch('addNumber', {
                    number: this.number,
                    contact_id: this.contact.id
                }).then(response => {
                    this.number = ''
                })
            }
        },
        computed: {
            numbers() {
                return this.$store.getters.getMyNumbers(this.contact.id);
            }
        },
        watch: {
            numbers: function (newQuestion, oldQuestion) {
                // console.log(newQuestion);
            }
        }
    }

</script>
<style scoped>
  .contact {
    margin-bottom: 10px;
    /*display: flex;*/
    align-items: center;
    justify-content: space-between;
  }

  .numberq {
    margin-bottom: 5px;
  }

  .showButton {
    width: 80%;
  }
</style>



