import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
axios.defaults.baseURL = 'http://contacts-notebook.test/api'
//axios.defaults.baseURL = 'http://127.0.0.1:8000/api'
//axios.defaults.baseURL = 'http://localhost/api'

export const store = new Vuex.Store({
  state: {
    token: localStorage.getItem('access_token') || null,
    filter: 'all',
    todos: [],
    contacts: [],
  },
  getters: {
    getMyContacts(state) {
      return state.contacts;
    },
    getMyNumbers: (state) => (contact_id) => {
      const tmp = state.contacts.find(contact => contact.id == contact_id);
      return tmp.numbers;
    },
    loggedIn(state) {
      return state.token !== null
    },
    remaining(state) {
      return state.todos.filter(todo => !todo.completed).length
    },
    anyRemaining(state, getters) {
      return getters.remaining != 0
    },
    todosFiltered(state) {
      if (state.filter == 'all') {
        return state.todos
      } else if (state.filter == 'active') {
        return state.todos.filter(todo => !todo.completed)
      } else if (state.filter == 'completed') {
        return state.todos.filter(todo => todo.completed)
      }
      return state.todos
    },
    showClearCompletedButton(state) {
      return state.todos.filter(todo => todo.completed).length > 0
    },

  },
  mutations: {

    getMyNumbers(state, data) {
      const tmp = state.contacts.find(contact => contact.id == data.contact_id);
      const index = state.contacts.findIndex(item => item.id == data.contact_id)
      state.contacts.splice(index, 1, {
        id: tmp.id,
        user_id: tmp.user_id,
        first_name: tmp.first_name,
        last_name: tmp.last_name,
        address: tmp.address,
        numbers: data.datax
      })

    },
    addTodo(state, todo) {
      state.todos.push({
        id: todo.id,
        title: todo.title,
        completed: false,
        editing: false,
      })
    },
    updateTodo(state, todo) {
      const index = state.todos.findIndex(item => item.id == todo.id)
      state.todos.splice(index, 1, {
        'id': todo.id,
        'title': todo.title,
        'completed': todo.completed,
        'editing': todo.editing,
      })
    },
    deleteTodo(state, id) {
      const index = state.todos.findIndex(item => item.id == id)
      state.todos.splice(index, 1)
    },
    checkAll(state, checked) {
      state.todos.forEach(todo => (todo.completed = checked))
    },
    updateFilter(state, filter) {
      state.filter = filter
    },
    clearCompleted(state) {
      state.todos = state.todos.filter(todo => !todo.completed)
    },
    retrieveTodos(state, todos) {
      state.todos = todos
    },
    retrieveToken(state, token) {
      state.token = token
    },
    destroyToken(state) {
      state.token = null
    },
    clearTodos(state) {
      state.todos = []
    },
    retrieveMyContacts(state, contacts) {
      state.contacts = [];
      contacts.forEach(function (item, index) {
        state.contacts.push({
          id: item.id,
          user_id: item.user_id,
          first_name: item.first_name,
          last_name: item.last_name,
          address: item.address,
          numbers: []
        });
      })
    },
    addContact(state, contact) {
      state.contacts.push({
        id:contact.id,
        first_name: contact.first_name,
        last_name: contact.last_name,
        address: contact.address,
        numbers: []
      })
    },
    editNumber(state, number) {
      var myContact = state.contacts.find(contact => contact.id == number.contact_id);
      var index = state.contacts.findindex(contact => contact.id == number.contact_id);
      myContact.numbers.push(number);
      state.contacts.splice(index, 1, {
        first_name: myContact.first_name,
        second_name: myContact.last_name,
        address: myContact.address,
        numbers: myContact.numbers
      })
    },
    addNumber(state,data){
      var myContact = state.contacts.find(contact => contact.id == data.contact_id);
      var index = state.contacts.findIndex(contact => contact.id == data.contact_id);
      console.log(data);
      myContact.numbers.push(data);
      state.contacts.splice(index, 1, {
        id:myContact.id,
        first_name: myContact.first_name,
        last_name: myContact.last_name,
        address: myContact.address,
        numbers: myContact.numbers
      })
    },
  },
  actions: {
    addNumber(context,number){
      axios.defaults.headers['Authorization'] = 'Bearer ' + store.state.token
      axios.post('/numbers/add', {
        number: number.number,
        contact_id: number.contact_id,
      })
        .then(response => {
          context.commit('addNumber', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    editNumber(context, number) {
      axios.defaults.headers['Authorization'] = 'Bearer ' + store.state.token
      axios.patch('/numbers/' + number.id, {
        number: number.number,
        contact_id: number.contact_id,
      })
        .then(response => {
          context.commit('editNumber', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },

    retrieveMyContacts(context) {
      axios.defaults.headers['Authorization'] = 'Bearer ' + store.state.token
      axios.get('/contacts')
        .then(response => {
          context.commit('retrieveMyContacts', response.data);
        })
        .catch(error => {
          console.log(error)
        })
    },
    getMyNumbers(context, contact_id) {
      axios.get('/numbers/' + contact_id)
        .then(response => {
          context.commit('getMyNumbers', {contact_id: contact_id, datax: response.data})
        })
        .catch(error => {
          console.log(error)
        })
    },
    retrieveName(context) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token

      return new Promise((resolve, reject) => {
        axios.get('/user')
          .then(response => {
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    clearTodos(context) {
      context.commit('clearTodos')
    },
    register(context, data) {
      return new Promise((resolve, reject) => {
        axios.post('/register', {
          name: data.name,
          email: data.email,
          password: data.password,
        })
          .then(response => {
            resolve(response)
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    destroyToken(context) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token

      if (context.getters.loggedIn) {
        return new Promise((resolve, reject) => {
          axios.post('/logout')
            .then(response => {
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              resolve(response)
              // console.log(response);
              // context.commit('addTodo', response.data)
            })
            .catch(error => {
              localStorage.removeItem('access_token')
              context.commit('destroyToken')
              reject(error)
            })
        })
      }
    },
    retrieveToken(context, credentials) {
      return new Promise((resolve, reject) => {
        axios.post('/login', {
          username: credentials.username,
          password: credentials.password,
        })
          .then(response => {
            const token = response.data.access_token

            localStorage.setItem('access_token', token)
            context.commit('retrieveToken', token)
            resolve(response)
          })
          .catch(error => {
            console.log(error)
            reject(error)
          })
      })
    },
    retrieveTodos(context) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
      axios.get('/todos')
        .then(response => {
          context.commit('retrieveTodos', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    addTodo(context, todo) {
      axios.post('/todos', {
        title: todo.title,
        completed: false,
      })
        .then(response => {
          context.commit('addTodo', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    updateTodo(context, todo) {
      axios.patch('/todos/' + todo.id, {
        title: todo.title,
        completed: todo.completed,
      })
        .then(response => {
          context.commit('updateTodo', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    },
    deleteTodo(context, id) {
      axios.delete('/todos/' + id)
        .then(response => {
          context.commit('deleteTodo', id)
        })
        .catch(error => {
          console.log(error)
        })
    },
    checkAll(context, checked) {
      axios.patch('/todosCheckAll', {
        completed: checked,
      })
        .then(response => {
          context.commit('checkAll', checked)
        })
        .catch(error => {
          console.log(error)
        })
    },
    updateFilter(context, filter) {
      context.commit('updateFilter', filter)
    },
    clearCompleted(context) {
      const completed = context.state.todos
        .filter(todo => todo.completed)
        .map(todo => todo.id)

      axios.delete('/todosDeleteCompleted', {
        data: {
          todos: completed
        }
      })
        .then(response => {
          context.commit('clearCompleted')
        })
        .catch(error => {
          console.log(error)
        })
    },
    addContact(context, contact) {
      axios.post('/contacts', {
        first_name: contact.first_name,
        last_name: contact.last_name,
        address: contact.address,
      })
        .then(response => {
          context.commit('addContact', response.data)
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
})



