<template>
  <div>
    <section>
      <b-tabs v-model="activeTab">
        <b-tab-item label="contacts">
          <div class="container ">
          <app-contact v-for="contact in contacts" :key="contact.id" :contact="contact"></app-contact>
          </div>
        </b-tab-item>

        <b-tab-item label="add new contact">
          <form @submit.prevent="addContact">
            <b-field label="first_name" value="john@">
              <b-input v-model="form.first_name"></b-input>
            </b-field>
            <b-field label="last_name" value="john@">
              <b-input v-model="form.last_name"></b-input>
            </b-field>
            <b-field label="address" value="john@">
              <b-input v-model="form.address"></b-input>
            </b-field>
            <b-button tag="input"
                      native-type="submit"
                      value="Submit input"/>
          </form>
        </b-tab-item>

        <b-tab-item label="search">
          <app-search :contacts="contacts" :activeTab="activeTab"></app-search>
        </b-tab-item>
      </b-tabs>
    </section>

  </div>
</template>

<script>
    import AppContact from './Contact'
    import AppSearch from './Search'
    import debounce from 'lodash/debounce'

    export default {
        components: {
            AppContact,AppSearch,
        },
        created() {
            this.$store.dispatch('retrieveMyContacts');
        },
        computed: {
            contacts() {
                return this.$store.getters.getMyContacts;
            },
            // filteredContacts() {
            //     const words=name.split(' ');
            //     const fname=words[0];
            //     const lname=words[1];
            //     return this.contacts.filter((option) => {
            //         option.first_name==fname&&option.last_name==lname
            //         // var x = option.first_name + ' ' + option.last_name
            //         // console.log(x);
            //         // return x
            //         //     .toString()
            //         //     .toLowerCase()
            //         //     .indexOf(this.name.toLowerCase()) >= 0
            //     })
            // }
        },
        data() {
            return {
                form: {
                    first_name: '',
                    last_name: '',
                    address: '',
                },
                activeTab: 0,
                name: '',
                selected: null,
                keepFirst: false,
                openOnFocus: false,
                data: [],
                isFetching: false
            }
        },
        methods: {


        addContact() {
            this.$store.dispatch('addContact', {
                first_name: this.form.first_name,
                last_name: this.form.last_name,
                address: this.form.address,
            }).then(response => {
                this.form = {
                    first_name: '',
                    last_name: '',
                    address: '',
                }
                this.activeTab=0
            })
        }
    },
    }
</script>
<style lang="scss">

</style>



